.adventures{
    &__header{
        border-bottom: 1px dashed #c8c8c8;
        margin-bottom: 3rem;
        padding-bottom: 1.5rem;
        text-align: center;
    }
    &__title{
        font-size: 2.4rem;
        margin-bottom: .5rem;
        margin-top: 0;
    }
    &__grid{
        display: grid;
        grid-template-columns: repeat(2,1fr);
        grid-gap: 1.5rem;
        margin-bottom: 4.5rem;
    }
    &__item{
        position: relative;
        &::before{
                content: "";
                float: left;
                padding-top: 100%;
        }
        img{
            left: 50%;
            height: 550px;
            max-width: none;
            width: auto;
            position: absolute;
            top: 50%;
            transform: translate(-50%,-50%);
            z-index: 1;
        }
    }
    .story-wrapper {
        bottom: 0;
        left: 0;
        overflow: hidden;
        position: absolute;
        right: 0;
        top: 0;
        z-index:5;
        &::after{
            background: rgba(0,0,0,.35);
            content: "";
            height: 100%;
            left: 0;
            position: absolute;
            top: 0;
            width: 100%;
            z-index: 1;
        }
    }
    .story-info {
        margin: 4.5rem 1.5rem;
        position: absolute;
        z-index: 2;
    }
    h2 {
        font-family: $font__main;
        font-size: 48px;
        font-size: 3rem;
        font-weight: 400;
        margin-bottom: 4.5rem;
        text-transform: none;
        a{
            color: #ffffff;
        }
    }
    .white-btn{
        border: 1px solid #fff;
        color: #fff;
        background-color: transparent;
        &:hover{
            background-color: $color__green;
        }
    }
}

.adventure{
    .entry-header{
        img{
        width: 100%;
    }
    }
    .content-wrapper{
        max-width: 800px;
    }
    .entry-title{
        margin-top: 3rem;
    }
    .entry-meta{
        font-family: $font__accent-sub;
    font-size: 19.2px;
    font-size: 1.2rem;
    text-transform: uppercase;
}
    }